import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const ADD_MODE = 'addSelectedMode';
export const UPDATE_MODES = 'updateSelectedModes';

export default new Vuex.Store({
    state: {
        selectedModes: new Set(['present polite']), // default mode
    },
    mutations: {
        [ADD_MODE](state, mode) {
            state.selectedModes.add(mode);
        },
        [UPDATE_MODES](state, modes) {
            state.selectedModes = new Set(modes);
        }
    },
    actions: {
        addSelectedModeAction(context, mode) {
            context.commit(ADD_MODE, mode);
        },
        updateSelectedModeAction(context, modes) {
            context.commit(UPDATE_MODES, modes);
        }
    },

    /*
     getter: {},
     modules: {},
     */
})
